main {
  justify-content: center;
}

section {
  align-items: center;
  justify-content: center;
  margin: auto;
  margin-top: 10px;
  width: 43em;
}

.name {
  font-size: 60px;
  text-align: center;
  padding-top: 25px;
  text-shadow: 3px 3px black;
}

.occupation {
  font-size: 25px;
  text-align: center;
  text-shadow: 2px 2px black;
}

.about {
  margin: auto;
  box-shadow: 0 0 1rem rgb(0, 0, 0, 0.5);
  width: 45em;
  background: inherit;
  text-shadow: 2px 2px black;
}

h3 {
  color: black;
}

.description {
  text-indent: 50px;
  width: 45em;
  text-align: center;
  margin: auto;
  color: white;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 18px;
  box-shadow: inset 0 0 2000px rgba(10, 10, 10, 0.5);
  border-radius: 5px;
}

@media only screen and (max-width: 1200px) {
  .about {
    width: 80%;
  }
  .description {
    width: 100%;
    margin: 0px 0px 0px 0px;
  }
}

@media only screen and (max-width: 820px) {
  .about {
    width: 80%;
  }
  .description {
    width: 100%;
    margin: 0px 0px 0px 0px;
    font-size: 25px;
  }
  .name {
    font-size: 45px;
    text-align: center;
    padding-top: 5px;
  }
  .occupation {
    font-size: 30px;
    text-align: center;
  }
}

@media only screen and (max-width: 480px) {
  .name {
    font-size: 35px;
    text-align: center;
    padding-top: 5px;
  }
  .occupation {
    font-size: 20px;
    text-align: center;
  }
  
  .about {
    width: 50%;
  }

  .description {
    width: 100%;
    font-size: 25px;
  }

  input {
    width: 50%;
  }

  textarea {
    width: 50%;
  }
}
